import * as React from "react"
import { Link, graphql } from "gatsby"
import Layout from "../components/layout/Layout"
import Seo from "../components/seo/Seo"
import PostCard from "../components/blog/PostCard"

const TagTemplate = ({ data, pageContext }) => {
  const { tag } = pageContext
  const posts = data.allMdx.nodes

  return (
    <Layout>
      <div className="max-w-[1200px] mx-auto px-4 py-12">
        <div className="mb-8">
          <Link to="/blog" className="text-[var(--color-editorial-accent)] hover:underline text-sm">
            ‚Üê Back to Blog
          </Link>
          <h1
            className="text-[var(--color-editorial-black)]"
            style={{
              fontSize: "2.25rem",
              fontWeight: 700,
              marginTop: "1rem",
              marginBottom: "0.5rem",
            }}
          >
            Tag: {tag}
          </h1>
          <p className="text-[var(--color-editorial-grey)] text-sm">
            {posts.length} {posts.length === 1 ? "post" : "posts"} tagged with "{tag}"
          </p>
        </div>

        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
          {posts.map(post => (
            <PostCard
              key={post.id}
              title={post.frontmatter.title}
              excerpt={post.excerpt}
              image={post.frontmatter.cover}
              slug={post.fields.slug}
              date={post.frontmatter.date}
              category={post.frontmatter.category}
            />
          ))}
        </div>
      </div>
    </Layout>
  )
}

export const Head = ({ pageContext }) => {
  return (
    <Seo
      title={`Posts tagged "${pageContext.tag}"`}
      description={`Browse all posts tagged with "${pageContext.tag}"`}
    />
  )
}

export default TagTemplate

export const pageQuery = graphql`
  query TagQuery($tag: String!) {
    allMdx(filter: { frontmatter: { tags: { in: [$tag] } } }, sort: { frontmatter: { date: DESC } }) {
      nodes {
        id
        excerpt(pruneLength: 160)
        fields {
          slug
        }
        frontmatter {
          title
          date(formatString: "MMMM DD, YYYY")
          category
          cover
          tags
        }
      }
    }
  }
`
